<template>
  <ul class="m-breadcrumb">
    <li
      class="m-breadcrumb-item"
      v-for="(item, index) in data"
      :key="item[unid]"
      @click.stop="$emit('change', item, index)"
    >
      <a href="javascript://">{{ item[label] }}</a>
    </li>
  </ul>
</template>

<script>
export default {
  name: "m-breadcrumb",

  props: {
    label: {
      type: String,
      default: "title"
    },
    unid: {
      type: String,
      default: "key"
    },
    data: {
      type: Array,
      default: () => []
    }
  }
};
</script>

